.homesite-main{
    height: fit-content;
    min-height: var(--content-height);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.homesite-display-name{
    height: 45vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 2em;
}

.homepage-container{
    height: fit-content;
    min-height: calc(var(--content-height) - 45vh);
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}

.homesite-course-container {
    height: fit-content;
    min-height: calc(var(--content-height) - 45vh);
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start; 
    align-items: flex-start; 
}

.homesite-course-in{
    width: 51%;
    height: fit-content;
    min-height: calc(var(--content-height) - 45vh);
    border-right: solid black 1px;
}

.homesite-course-info{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
    width: 100%;
    height: 7vh;
    font-size: 1.5em;
}

@media screen and (max-width: 600px) {
    .homepage-container{
        flex-direction: column;
    }
    .homesite-course-in{
        width: 100%;
        border: none;
    }
    .homesite-course-container{
        width: 100%;
    }
    .homesite-display-name{
        font-size: 1.5em;
        padding-left: 24vw
    }
}