.navbar-main{
    height: var(--navbar-height);
    width: 100%;
    background-color: var(--navbar-color);
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 100px black;
}

.navbar-a{
    box-shadow: 0px 0px 3px 5px rgba(0, 0, 0, 0.619);
    z-index: 200;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar-left{
    display: flex;
    height: 100%;
    width: 50%;
    justify-content: flex-start;
    align-items: center;
}

.navbar-left button{
    height: 100%;
    background-color: transparent;
    border: none;
}

.navbar-left img{
    height: 100%;
    cursor: pointer;
}

.navbar-right{
    display: flex;
    height: 100%;
    width: 50%; 
    justify-content: flex-end;
    align-items: center;
    padding-right: 4vh;
}

.navbar-user-icon{
    height: calc(var(--navbar-height) / 10 * 7);
    width: calc(var(--navbar-height) / 10 * 7);
    border-radius: 50%;
    overflow: hidden;
    cursor: pointer;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.navbar-user-icon img{
    height: 70%;
}

.navbar-login-button{
    height: calc(var(--navbar-height) / 10 * 5.6);
    width: calc(var(--navbar-height)*1.5);
    border: none;
    font-size: larger;
    border-radius: 5px;
    cursor: pointer;
    background-color: white;
    text-decoration: none;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}